/* * * * 투두리스트 * * * */
// 입력
const todoInput = document.getElementById("todo-input");
todoInput.addEventListener("keydown", submitToDo);

function submitToDo(event) {
  if (event.keyCode == 13) {
    const newToDo = todoInput.value;
    todoInput.value = "";
    const newToDoObj = {
      text: newToDo,
      id: Date.now()
    };
    toDos.push(newToDoObj);
    paintToDo(newToDoObj);
    saveToDos();
  }
}

function paintToDo(newToDoObj) {
  const li = document.createElement("li");
  li.id = newToDoObj.id;
  const span = document.createElement("span");
  span.innerText = newToDoObj.text;
  const btn = document.createElement("button");
  btn.innerText = "✔";
  btn.style.border = "none";
  btn.style.margin = "5px";
  btn.addEventListener("click", deleteToDo);
  li.appendChild(span);
  li.appendChild(btn);
  toDoList.appendChild(li);
}

// 저장
const toDoList = document.getElementById("todo-list");
let toDos = [];
const savedToDos = localStorage.getItem("todos");

function saveToDos() {
  localStorage.setItem("todos", JSON.stringify(toDos));
}

if (savedToDos) {
  const parsedToDos = JSON.parse(savedToDos);
  toDos = parsedToDos;
  // forEach 함수는 paintToDo 함수를 parsedToDos의 배열의 요소마다 실행한다.
  parsedToDos.forEach(paintToDo);
}

// 삭제
function deleteToDo(event) {
  const li = event.target.parentElement;
  li.remove();
  toDos = toDos.filter((toDo) => toDo.id !== parseInt(li.id));
  saveToDos();
}

/* * * * 날씨, 위치 * * * */
function onGeoOk(position) {
  const lat = position.coords.latitude;
  const lng = position.coords.longitude;
  // https://openweathermap.org/api
  const myApi = "4b70fc63e8f3a09a8c86bdcae8a06beb";
  const url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lng}&appid=${myApi}`;
  // console.log(url);
  fetch(url)
    .then((response) => response.json())
    .then((data) => {
      const country = document.querySelector("#weather span:first-child");
      const name = document.querySelector("#weather span:last-child");
      country.innerText = `${data.name}, ${data.sys.country}`;
      name.innerText = `${data.weather[0].main} (${data.weather[0].description})`;
      // 일출, 일몰 시간 이용해서 welcome 메시지를 바꿔줘도 좋을 것 같음
    });
}

function onGeoError() {
  // console("Can't find you. No weather for you.");
}
navigator.geolocation.getCurrentPosition(onGeoOk, onGeoError);
